@import url(https://fonts.googleapis.com/css?family=Numans);
@import url(https://fonts.googleapis.com/css?family=Numans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Made with love by Mutiullah Samim*/
#root {
  height: 100%;
  font-family: 'Numans', sans-serif; }

.container {
  position: fixed;
  width: 100%;
  height: 100%;
  max-width: 100%;
  align-content: center;
  background-size: cover;
  background-repeat: no-repeat; }

.card {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 370px;
  width: 400px;
  margin: auto;
  background-color: green !important; }

.social_icon span {
  font-size: 60px;
  margin-left: 10px;
  color: #FFC312; }

.social_icon span:hover {
  color: white;
  cursor: pointer; }

.social_icon span i {
  position: relative; }

.social_icon span i:before {
  position: relative;
  z-index: 2; }

.social_icon span i:after {
  content: '';
  position: absolute;
  width: 45px;
  height: 45px;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: green;
  z-index: 1; }

.card-header h3 {
  color: white; }

.social_icon {
  position: absolute;
  right: 20px;
  top: -45px; }

.input-group-prepend span {
  width: 50px;
  background-color: #FFC312;
  color: black;
  border: 0 !important; }

input:focus {
  outline: 0 0 0 0  !important;
  box-shadow: 0 0 0 0 !important; }

.remember {
  color: white; }

.remember input {
  width: 20px;
  height: 20px;
  margin-left: 15px;
  margin-right: 5px; }

.login_btn {
  color: black;
  background-color: #FFC312;
  width: 100px; }

.login_btn:hover {
  color: black;
  background-color: white; }

.links {
  color: white; }

.links a {
  margin-left: 4px; }

a {
  color: #ffed00;
  text-decoration: none;
  background-color: transparent; }

a:hover {
  color: #ffb2b2;
  text-decoration: underline; }

